.login {
  margin: 3rem auto;
  width: 95%;
  max-width: 25rem;
  border-radius: 6px;
  background-color: white;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.5);
  padding: 2rem;
  text-align: center;
}

.login h1 {
  text-align: center;
  color: rgb(29, 29, 175);
}

.control {
  margin-bottom: 0.5rem;
}

.control label {
  display: block;
  color: black;
  font-weight: 500;
  margin-bottom: 0.5rem;
}

.control input {
  font: inherit;
  background-color:white;
  color: black;
  border-radius: 4px;
  border: 1px solid black;
  width: 90%;
  text-align: left;
 padding:0.25rem;
 border-color:rgb(29, 29, 175);
}
.actions {
  margin-top: 1.5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.actions button {
  cursor: pointer;
  font: inherit;
  color: white;
  background-color: rgb(29, 29, 175);
  border: 1px solid rgb(29, 29, 175);
  border-radius: 4px;
  padding: 0.5rem 2.5rem;
}

.actions button:hover {
  color:white;
  background-color: rgba(25, 25, 124, 0.74);
  border-color:rgba(25, 25, 124, 0.74);
}

